import {LOGIN_FAILURE, LOGIN_REQUEST, LOGIN_SUCCESS, LOGOUT_SUCCESS} from "../actions/types";

const INITIAL_STATE = {
    isFetching: false,
    isAuthenticated: localStorage.getItem('access_token') ? true : false,
    errorMessage: '',
    user: null,
    id_token: null,
    login_success: false,
};

export default (state = INITIAL_STATE, action) => {
    switch(action.type){
        case LOGIN_REQUEST:
            return {...state, isFetching: true, isAuthenticated: false, user: action.payload};
        case LOGIN_SUCCESS:
            return {...state, isFetching: false, isAuthenticated: true, errorMessage: ''};
        case LOGIN_FAILURE:
            console.log(action);
            return {...state, isFetching: false, isAuthenticated: false, errorMessage: action.errorMessage};
        case LOGOUT_SUCCESS:
            return {...state, login_success: true, isFetching: true, isAuthenticated: false};
        default:
            return state;
    }
};