import {ARTICLE_INFO, ARTICLES_RECEIVED} from "../actions/types";

const INITIAL_STATE = {
    article_info: null,
    articles: [],
};

export default (state = INITIAL_STATE, action) => {
    switch(action.type){
        case ARTICLE_INFO:
            console.log(action.payload);
            return  {...state, article_info: action.payload};
        case ARTICLES_RECEIVED:
            return {...state, articles: action.articles};
        default:
            return state;
    }
};
