import React,{Component} from "react";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import {Route} from "react-router-dom";
import Col from "react-bootstrap/Col";
import connect from "react-redux/es/connect/connect";
import {getArticles, gotoArticle, deleteArticle} from "../actions/article";
import Card from "react-bootstrap/Card";

class ManagementPage extends Component{
    constructor(){
        super();
        this.state = ({
            createArticle: false,
        });
    }

    componentDidMount(){
        // Make sure to get all - manage
        this.props.getArticles();
    }

    render(){
        return(
            <Container style={{padding:"5rem"}}>
                <h2>Manage articles</h2>
                <Row>
                    <Col className="col-sm-3">
                    <Route render={({history}) => (
                        <Button size="sm" onClick={() => history.push('/create')} >New Article</Button>
                    )}/>
                    </Col>
                    <Col className="col-sm-5">
                        <Container>
                        {this.props.articles.map((article,index) => (
                            <Card key={index} className="mt-1" style={{border:'none'}}>
                                <Card.Title className="mt-3 ml-5">{article.title}</Card.Title>
                                <Card.Body>
                                    <Button className="m-1"
                                        onClick={() => {
                                            this.props.deleteArticle(article._id);
                                            this.props.getArticles();
                                        }}
                                    >Delete</Button>
                                    <Button className="m-1">Edit</Button>
                                </Card.Body>
                            </Card>
                        ))}
                        </Container>


                    </Col>
                </Row>
            </Container>
        );
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const auth = state.AuthReducer;
    return {
        articles: article.articles,
        isAuthenticated: auth.isAuthenticated
    }
}

export default connect(mapStateToProps,{
    getArticles,
    gotoArticle,
    deleteArticle
})(ManagementPage);