import ArticleBase from "./ArticleBase";
import connect from "react-redux/es/connect/connect";
import {getArticles, gotoArticle, getArticle} from "../actions/article";
import Figure from "react-bootstrap/Figure";
import Container from "react-bootstrap/Container";
import React, {Component} from "react";
import MainPage from "../components/MainPage";

class ArticleTemplate6 extends Component{

    constructor(){
        super();
    }

    render(){
        return (
            <Container>
            <h2>{this.props.article_info.title}</h2>
            <p>{this.props.article_info.lead}</p>
            <p>{this.props.article_info.tag}</p>
            <Figure>
                <Figure.Image
                    src={this.props.article_info.img_url}
                />
            </Figure>
            <p>{this.props.article_info.text}</p>
            </Container>

        );
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const auth = state.AuthReducer;
    return {
        //article_info: article.article_info,
        //articles: article.articles,
        isAuthenticated: auth.isAuthenticated
    }
}

export default connect(mapStateToProps,{
    getArticle,
    getArticles,
    gotoArticle
})(ArticleTemplate6);