import {Component} from "react";
import Container from "react-bootstrap/Container";
import React from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import {loginUser, logoutUser} from "../actions/authentication";
import connect from "react-redux/es/connect/connect";
import {getArticles, gotoArticle} from '../actions/article';
import {
    Link
} from "react-router-dom";
import {FacebookShareButton, TwitterShareButton, FacebookIcon, TwitterIcon} from 'react-share';

class MainPage extends Component {

    constructor(){
        super();
    }

    componentDidMount(){
        this.props.getArticles();
    }

    renderArticle(article){
        this.props.gotoArticle(article)
    }

    render(){
        return (
            <Container>
                {this.props.articles.map((article,index) => (
                    <Card key={index} className="mt-4" style={{border:'none'}} /*className={index===0 ? "mt-4" : "mt-4"}*/ /*style={{height:index===0 ? '' : '10rem', display: 'flex', flexDirection: 'column'}}*/>

                        <Card.Title className="mt-3 ml-5" style={{fontSize:"1.5rem"}}>{article.title}</Card.Title>
                        <Container>
                        <a className="ml-3">
                            {new Date(article.date).toDateString()}
                        </a>
                        <a href="#" onClick={(event) => {
                    event.preventDefault();
                    event.stopPropagation();}} style={{color:'gray', marginLeft:"2rem", fontWeight:'bold'}}>
                            {article.tags}
                        </a>

                        </Container>
                        <Card.Body className="row">
                            <Card.Img onClick={() => console.log("img clicked")} className="img-fluid col-md-4" src={article.img_url} /> {/*img-thumbnail*/}
                            <Card.Text className="col-md-5 m-2">{article.lead}</Card.Text>
                            <Link className="col-md-2 mr-5 text-right" to={"/article/"+article._id} onClick={() => this.renderArticle(article)}>Read more</Link>
                        </Card.Body>
                    </Card>
                ))}
            </Container>
        )
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const auth = state.AuthReducer;
    return {
        articles: article.articles,
        isAuthenticated: auth.isAuthenticated
    }
}

export default connect(mapStateToProps,{
    getArticles,
    gotoArticle
})(MainPage);