import {ARTICLES_RECEIVED, ARTICLE_INFO} from "../actions/types";
const { stringify } = require('querystring');

function receiveArticles(_articles){
  return {
    type: ARTICLES_RECEIVED,
    articles: _articles
  }
}

function setArticleInfo(article){
    return {
        type: ARTICLE_INFO,
        payload: article
    }
}

export function gotoArticle(article){

    return (dispatch) => {
        dispatch(setArticleInfo(article));
    }
}

