import React,{Component} from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
import ToggleButton from "react-bootstrap/ToggleButton";
import Figure from "react-bootstrap/Figure";
import {FacebookIcon, FacebookShareButton, TwitterIcon, TwitterShareButton, LinkedinIcon, LinkedinShareButton} from "react-share";
import connect from "react-redux/es/connect/connect";
import {getArticle, getArticles, gotoArticle} from "../actions/article";

class SimpleArticle extends Component{
    constructor(){
        super();

        this.state={
            paragraphs: 0,
            text: "",
            img_url: "",
            date:new Date(),
        };

        this.renderParagraphs.bind(this);
        this.renderEditor.bind(this);
        this.renderArticle.bind(this);
    }

    componentDidUpdate(prevProps, prevState){
        if(prevState.text !== this.state.text || prevState.img_url !== this.state.img_url) {
            //console.log("text/img updated");
            this.props.layoutDataCallback({text: this.state.text, img_url: this.state.img_url});
        }
    }

    renderArticle(){
        return(
            <Container className="bg-white mt-4 p-sm-1">
            <div>
                <h2>{this.props.editArticle ? this.props.title : this.props.article_info.title}</h2>
                <p>{this.props.editArticle ? this.props.lead : this.props.article_info.lead}</p>
                <p>{this.state.date.toDateString()}, By {this.props.editArticle ? this.props.author : this.props.article_info.author}
                </p>
                <Figure>
                    <Figure.Image
                        src={this.props.editArticle ? this.state.img_url : this.props.article_info.img_url} // WILL GENERATE ERRORS
                    />
                </Figure>
                {this.props.editArticle ? this.state.text.split ('\n').map ((item, i) => <p key={i}>{item}</p>) :
                    this.props.article_info.text.split ('\n').map ((item, i) => <p key={i}>{item}</p>)
                }
                Sources:
                <p>
                {this.props.editArticle ? this.props.sources : this.props.article_info.sources}
                </p>
            </div>
                <div>
                <Row className="row-cols-xs-1 bg-light p-4">
                     <FacebookShareButton className="m-2" url={window.location.href}><span><FacebookIcon size={32}/></span></FacebookShareButton>
                     <TwitterShareButton className="m-2" url={window.location.href}><span><TwitterIcon size={32}/></span></TwitterShareButton>
                     <LinkedinShareButton className="m-2" url={window.location.href}><span><LinkedinIcon size={32}/></span></LinkedinShareButton>
                    <a  className="p-2 m-1 bg-secondary" style={{color:'white'}} href="#" onClick={(event) => {
                    event.preventDefault();
                    event.stopPropagation();}}>
                    {this.props.editArticle ? this.props.tag : this.props.article_info.tags}</a>

                </Row>
                </div>
            </Container>
        );
    }

    renderParagraphs(){
        let paragraphs = [];
        for(let i=0; i<this.state.paragraphs;i++){
            paragraphs.push(
            <Row>
            <Col md={10}>
            <InputGroup key={i}>
                <InputGroup.Prepend>
                    <InputGroup.Text>Paragraph {i+2}</InputGroup.Text>
                </InputGroup.Prepend>
                <FormControl as="textarea" aria-label="Paragraph"/>
            </InputGroup>
            </Col>
            <Col md={2}>
               <Button className="btn-danger" size="sm" onClick={() => this.setState({
                   paragraphs: this.state.paragraphs-1,
               })} >x</Button>
            </Col>
            </Row>
            );
        }

        return (<Container>{paragraphs}</Container>);
    }

    renderEditor(){
        return(
            <div style={{padding:0,margin:0,width:"100%"}}>
                <InputGroup style={{height:"15rem"}}>
                    <InputGroup.Prepend>
                    <InputGroup.Text>Article text</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        onChange={(event) => this.setState({text:event.target.value})}
                        as="textarea"
                        aria-label="Paragraph"
                    />
                </InputGroup>
                   <Button disabled={true} size="sm" onClick={() => this.setState({
                       paragraphs: this.state.paragraphs+1,
                   })} >Add</Button>


                {this.renderParagraphs()}


                <InputGroup className="mt-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="label">Image url:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        onChange={(event) => this.setState({img_url:event.target.value})}
                        placeholder="image url"
                        aria-label="image url"
                        aria-describedby="label"
                    />
                </InputGroup>

            </div>
        );
    }

    render(){
        return(
        <div>
        {this.props.editArticle &&
            this.renderEditor()
        }
        {this.props.showArticle &&
            this.renderArticle()
        }
        </div>
        );
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    return {
        article_info: article.article_info,
    }
}

export default connect(mapStateToProps,{
})(SimpleArticle);