import {
    LOGIN_FAILURE,
    LOGIN_REQUEST,
    LOGIN_SUCCESS,
    LOGOUT_SUCCESS,
    LOGOUT_REQUEST
} from "../actions/types";
const { stringify } = require('querystring');

function requestLogin(creds) {
  return {
    type: LOGIN_REQUEST,
    isFetching: true,
    isAuthenticated: false,
    user: creds
  }
}

function receiveLogin(user) {
  return {
    type: LOGIN_SUCCESS,
    isFetching: false,
    isAuthenticated: true,
    id_token: user.id_token
  }
}

function loginError(message) {
  return {
    type: LOGIN_FAILURE,
    isFetching: false,
    isAuthenticated: false,
    errorMessage: message,
  }
}

function requestLogout() {
  return {
    type: LOGOUT_REQUEST,
    isFetching: true,
    isAuthenticated: true
  }
}

function receiveLogout() {
  return {
    type: LOGOUT_SUCCESS,
    isFetching: false,
    isAuthenticated: false
  }
}

export function logoutUser(){
  return dispatch => {
    dispatch(requestLogout())
    //localStorage.removeItem('id_token')
    localStorage.removeItem('access_token')
    dispatch(receiveLogout())
  }
}

export function loginUser(creds){
    console.log(creds.username);

    let config = {
        method: 'POST',
        headers: { 'Content-Type':'application/x-www-form-urlencoded'},
        body: stringify({name:creds.name,password:creds.password})
    }
//`name=${creds.username}&password=${creds.password}`
    return (dispatch) => {
        console.log("dispatching");
        dispatch(requestLogin(creds));
        return fetch('https://aqueous-coast-74759.herokuapp.com/login', config)
          .then(response =>
            response.json()
                .then(user => ({ user, response }))
                ).then(({ user, response }) =>  {

    // TODO Fall into response not ok, all is ok atm
            if (response.status !== 200) {
              // If there was a problem, we want to
              // dispatch the error condition
                console.log("login not ok");

              //dispatch(loginError(user.message));
              return Promise.reject(user)
            } else {
              // If login was successful, set the token in local storage
                console.log(user.access_token);
              localStorage.setItem('access_token', user.access_token);
              // Dispatch the success action
              dispatch(receiveLogin(user));
            }
      }).catch(err => dispatch(loginError(err.message)))
    }

}