import {Component, useEffect} from "react";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import React from "react";
import connect from "react-redux/es/connect/connect";
import {getArticles, gotoArticle, getArticle} from "../actions/article";
import ArticleTemplate6 from "../templates/ArticleTemplate6";
import {
    Link,
    useRouteMatch,
} from "react-router-dom";
import {FacebookIcon, FacebookShareButton, TwitterIcon, TwitterShareButton} from "react-share";
import {useRouter} from '../Routes';
import SimpleArticle from "../templates/SimpleArticle";

class ArticlePage extends Component {

    constructor() {
        super();
    }

    componentWillMount(){
        if(this.props.article_info == null){
            let query = window.location.pathname.split('/article/')[1];
            this.props.getArticle(query);
        }
        if(this.props.articles.length === 0){
            this.props.getArticles();
        }

        window.scrollTo(0, 0);

        // Handle browser back button
        window.onpopstate = () => {
            if(window.location.pathname.startsWith('/article/')){

                let query = window.location.pathname.split('/article/')[1];
                let article = this.props.articles.find(element => element._id === query);

                if(article !== null){
                    this.props.gotoArticle(article);
                    console.log("article exist")
                }
                else{
                    this.props.getArticle(query);

                }
            }
        }
    }

    renderArticle() {
        if(this.props.article_info) {
            switch (this.props.article_info.template) {
                case "Simple":
                    return (<SimpleArticle
                        editArticle={false}
                        showArticle={true}
                        title={this.props.article_info.title}
                        author={this.props.article_info.author}
                        lead={this.props.article_info.lead}
                        sources={this.props.article_info.sources}
                        tag={this.props.article_info.tags}
                        layoutDataCallback={(data) => console.log(data)}
                    />)
                case 6:
                    return (<ArticleTemplate6 article_info={this.props.article_info}/>);
                default:
                    return (<ArticleTemplate6 article_info={this.props.article_info}/>);
            }
        }
    }

    handleGotoArticle(article){
        this.props.gotoArticle(article);
        window.scrollTo(0, 300);
    }

    renderArticlePreviews(){
        return(
            this.props.articles.map((article,index) => (
                    this.props.article_info._id !== article._id &&
                    <Card key={index} className="mt-4 bg-light" style={{border:'none'}} /*className={index===0 ? "mt-4" : "mt-4"}*/ /*style={{height:index===0 ? '' : '10rem', display: 'flex', flexDirection: 'column'}}*/>

                        <Card.Title className="mt-3 ml-5" style={{fontSize:"1.2rem"}}>{article.title}</Card.Title>

                        <Card.Body className="row">
                            <Card.Img onClick={() => console.log("img clicked")} className="img-fluid col-md-4" src={article.img_url} />
                            <Card.Text className="col">{article.lead}</Card.Text>
                            <Link className="col mr-0" to={"/article/"+article._id} onClick={() => this.handleGotoArticle(article)}>Read more</Link>
                        </Card.Body>
                    </Card>
                )
            )
        )
    }

    render() {
        return (
            <Container>
                <Container className="bg-white mt-4" style={{padding:"2rem"}}>
                    {this.renderArticle()}
                </Container>
                <Container style={{marginTop:"5rem"}}>
                    <h4>More articles:</h4>
                    {this.renderArticlePreviews()}
                </Container>
            </Container>
        );

    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    return {
        articles: article.articles,
        article_info: article.article_info,
    }
}

export default connect(mapStateToProps,{
    getArticle,
    getArticles,
    gotoArticle
})(ArticlePage);